import { User } from './../../providers/user/user';
import { MyApp } from './../../app/app.component';
import { ChecklistPage, LoginPage } from './../index';
import { VeiculoService } from './../../providers/api/veiculo.service';
import { DashboardService } from './../../providers/api/dashboard.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the DashboardEntregasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dashboard-entregas',
  templateUrl: 'dashboard-entregas.html',
})
export class DashboardEntregasPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public dashboardService:DashboardService,
    public veiculoService: VeiculoService,
    public user: User,
    public app: MyApp
  ) {

    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DashboardEntregasPage');
  }


  abrirCheckList() {
    this.navCtrl.setRoot(ChecklistPage);
  }

  logout() {
    this.user.logout();
    this.app.getRootNav().setRoot(LoginPage);
  }

}



